import react, { useEffect } from 'react'
import {useHistory} from 'react-router-dom'
import {useState} from 'react'
import search from './icons/search.svg'
import logo from './icons/logo.png'
import  './NavBar.css'
const NavBar = (props) => {
    const [barToggle,setBarToggle] = useState(false)
    const [closeBtnToggle,setCloseBtnToggle] = useState(false)
     const [show,setHandleShow] = useState(false)
    const history = useHistory()
   
    const transitionNavBar = () => {
        if(window.scrollY > 100){
            setHandleShow(true)
        }else{
            setHandleShow(false)
        }
    }
    
    
    useEffect(() => {
        window.addEventListener("scroll",transitionNavBar)
        return(() => window.removeEventListener("scroll",transitionNavBar))
    },[])
        return (
        <div className = {`nav ${show && 'nav_black'}`}>

            <div className = "nav_contents">
                <img onClick = {() => history.push(process.env.PUBLIC_URL + "/")}  className = "nav_logo" 
                src = {logo} alt = "netflix-logo"></img>
                
            <div style = {{display:"flex"}}>
                <div style = {{width:barToggle? "250px": "0", border: barToggle? "1px solid white" : "0px solid white",backgroundColor:"black"}}className = "nav_rightWrapper">
                    <img onClick = {() => setBarToggle(!barToggle)} className = "nav_rightWrapper_img"  src = {search} alt ="search-icon"></img>
                    <input onChange = {(e) => props.onSearch(e)} style = {{width:barToggle? "200px" : "0%",padding: barToggle? "0 8px":"0"}} placeholder = "Search Movies, Tv Shows" className = "nav_rightWrapper_bar" type = "text">

                    </input>
                    <div style = {{display:barToggle? "block": "none"}} className = "nav_contents_closeBtn" onClick = {() => {
                        
                        setBarToggle(!barToggle)}}></div>
                    </div>    
                   
                    <img onClick = {() => history.push(process.env.PUBLIC_URL + "/profile")} className = "nav_avatar" 
                    src = "data:image/png;base64," alt = "netflix-avatar"></img>
                
            </div>
            </div>
        </div>
    )
}

export default NavBar; 